<template>
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
        <FormItem label="登录账号" prop="name">
            <Input v-model="formValidate.name" placeholder="Enter your name"></Input>
        </FormItem>

        <FormItem label="用户昵称" prop="cnname">
        <Input v-model="formValidate.cnname" placeholder="Enter your name"></Input>
    </FormItem>

        <FormItem label="E-mail" prop="email">
            <Input v-model="formValidate.email" type="email" placeholder="后续版本预留..."></Input>
        </FormItem>

        <FormItem label="手机号码" prop="mobile">
            <Input v-model="formValidate.mobile" placeholder="用于接收天气预警短信的手机号码..."></Input>
        </FormItem>

        <FormItem label="密码" prop="password">
            <Input v-model="formValidate.password" type="password" placeholder="Enter your password"></Input>
        </FormItem>

        <FormItem label="确认密码" prop="repassword">
            <Input v-model="formValidate.repassword" type="password" placeholder="Enter your password agin"></Input>
        </FormItem>

        <FormItem>
            <Button type="primary" @click="handleSubmit('formValidate')">Submit</Button>
            <Button @click="handleReset('formValidate')" style="margin-left: 8px">Reset</Button>
        </FormItem>


    </Form>
</template>
<script>
    export default {
        name:'register',
        data () {
            return {
                formValidate: {
                    name: '',
                    cnname:'',
                    email: '',
                    mobile:'',
                    password:'',
                    repassword:'',
                },
                ruleValidate: {
                    name: [
                        { required: true, message: '账号不能为空', trigger: 'blur' }
                    ],
                    cnname: [
                        { required: true, message: '用户昵称不能为空', trigger: 'blur' }
                    ],
                    email: [
                        { required: true, message: '邮箱地址不能为空', trigger: 'blur' },
                        { type: 'email', message: '无效的邮箱格式', trigger: 'blur' }
                    ],
                    password: [
                        {required:true, message:'password'}
                    ],
                    repassword: [
                        {required:true, message:'repassword'}
                    ],
                    mobile: [
                        {required:true, message:'手机号码不能为空', trigger:'blur'},
                        {max: 11,min:11, required: true, message: '只能输入11位数字',trigger:'blur'},
                        // { type: 'number', message: '只能数字', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            async register(){
                let res = await this.$Http.addUser(this.formValidate, true);
                console.log(res.data);
                if (res.data.rspCode === "000000") {
                    this.$Message.success('注册成功!');
                    this.$router.push('/')
                }

            },
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.register();
                    } else {
                        this.$Message.error('验证失败!');
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
            }
        }
    }
</script>
